.main-footer {
  font-family: $font;
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-footer a {
  color: #000000;
}

.main-footer__tag {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  margin: 29px 78px 34px 76px;
}

.main-footer__networks {
  display: flex;
}

@media (min-width: $tablet-width) {
  .main-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 64px;
  }

  .main-footer__tag {
    margin: 0;
  }
}
